.socials {
  .clear-list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 80px;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  fill: @socials-icon;

  &:hover,
  &:focus {
    fill: @tiffany-darker;
  }

  &:active {
    fill-opacity: 0.3;
  }
}
