@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus,
*::before:focus,
*::after:focus {
  outline: 2px solid #63d1bb;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 400;
  color: #444;
  text-align: left;
  background-color: #fff;
}
@media (min-width: 768px) {
  .page__body {
    display: grid;
    grid-template-rows: min-content 1fr min-content;
  }
}
@media (min-width: 768px) {
  .page__header {
    margin-bottom: -89px;
  }
}
@media (min-width: 768px) {
  .page__main {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.white-wave--index::after {
  bottom: -10px;
  background-size: 39px auto;
}
