.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 320px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0;
    width: 1050px;
  }
}
.link-icon {
  width: 20px;
  height: 20px;
  opacity: 0.2;
}
.link-icon:hover,
.link-icon:focus {
  fill: #63d1bb;
  opacity: 1;
}
.link-icon:active {
  fill-opacity: 0.3;
}
.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 14px 20px;
  font-weight: 700;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  background-color: #fff;
  border: 2px solid #444;
}
.button:hover,
.button:focus {
  color: #fff;
  background-color: #444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.header-link {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: #000;
  font-weight: 700;
}
.header-link[href]:hover,
.header-link[href]:focus {
  color: rgba(0, 0, 0, 0.6);
}
.header-link[href]:hover .header-link__icon,
.header-link[href]:focus .header-link__icon {
  fill: #63d1bb;
  opacity: 1;
}
.header-link[href]:hover .header-link__logo-img,
.header-link[href]:focus .header-link__logo-img {
  opacity: 0.6;
}
.header-link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.header-link[href]:active .header-link__icon {
  fill-opacity: 0.3;
}
.header-link[href]:active .header-link__logo-img {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header-link {
    box-shadow: none;
  }
}
.header-link__logo-img {
  display: block;
  width: 85px;
  height: 35px;
}
@media (min-width: 768px) {
  .header-link__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .header-link__logo-img {
    width: 149px;
    height: 72px;
  }
}
.header-link__icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  fill: #000;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .header-link__icon {
    left: 32px;
  }
}
@media (min-width: 768px) {
  .header-link__search-text {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    overflow: hidden;
  }
}
.main-title {
  margin: 0;
  padding: 0;
  line-height: 30px;
  font-size: 27px;
  font-weight: 700;
  color: #000;
  text-align: center;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .main-title {
    font-size: 53px;
    line-height: 57px;
  }
}
.section-title {
  margin: 0;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: #000;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section-title {
    font-size: 53px;
    line-height: 57px;
  }
}
.header {
  position: relative;
}
.header__nav {
  display: grid;
}
@media (min-width: 768px) {
  .header__nav {
    margin: 0 auto;
    padding: 0 30px;
    width: 320px;
    grid-template-columns: 319px 349px;
    grid-template-rows: min-content min-content;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .header__nav {
    padding: 0 50px;
    width: 768px;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .header__nav {
    padding: 0;
    width: 1050px;
  }
}
@media (min-width: 1150px) {
  .header__nav {
    grid-template-columns: 420px 1fr 348px;
  }
}
/* Кнопка открыть-закрыть меню */
.header__nav-toggle {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  width: 79px;
  height: 79px;
  border: none;
  background: url("../img/icons/menu-open.svg") center center no-repeat;
}
.header__nav-toggle--to-close {
  background-image: url("../img/icons/menu-close.svg");
}
@media (min-width: 768px) {
  .header__nav-toggle {
    display: none;
  }
}
.header__logo-link {
  justify-content: center;
}
@media (min-width: 768px) {
  .header__logo-link {
    min-height: 89px;
  }
}
@media (min-width: 1150px) {
  .header__logo-link {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.header__nav-list,
.header__order-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.header__nav-list--closed,
.header__order-list--closed {
  display: none;
}
.header__nav-item,
.header__order-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-shadow: 0 1px #ececec, inset 0 1px #ececec;
}
@media (min-width: 768px) {
  .header__nav-item,
  .header__order-item {
    z-index: 5;
    box-shadow: none;
    min-height: 89px;
  }
}
@media (min-width: 768px) {
  .header__nav-list {
    grid-column: 1 / 2;
    grid-row: 2 / -1;
    display: flex;
    flex-wrap: wrap;
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .header__nav-list {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
    font-size: 17px;
  }
}
@media (min-width: 768px) {
  .header__nav-item {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    width: 150px;
    justify-content: flex-start;
  }
  .header__nav-item:nth-of-type(2n+1) {
    margin-right: 17px;
  }
}
@media (min-width: 1150px) {
  .header__nav-item {
    width: 167px;
  }
  .header__nav-item:nth-last-of-type(-n+2) {
    align-items: flex-start;
    font-size: 15px;
  }
  .header__nav-item:nth-of-type(2n) {
    padding-left: 36px;
  }
  .header__nav-item:nth-of-type(2) {
    flex-grow: 1;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}
.header__nav-item--desktop-only {
  display: none;
}
@media (min-width: 1150px) {
  .header__nav-item--desktop-only {
    display: flex;
  }
}
@media (min-width: 768px) {
  .header__order-list {
    grid-row: 1 / 3;
    grid-column: 2 / 3;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: flex-start;
  }
}
@media (min-width: 1150px) {
  .header__order-list {
    grid-column: 3 / 4;
    grid-row: 1 / 3;
  }
}
.header__order-item {
  align-items: center;
}
@media (min-width: 768px) {
  .header__order-item {
    align-items: flex-start;
    min-height: 89px;
  }
  .header__order-item:nth-child(-n+2) {
    align-items: center;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}
.header__order-item--delivery {
  display: none;
}
@media (min-width: 768px) {
  .header__order-item--delivery {
    display: flex;
  }
}
.header__link-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 260px;
  min-height: 79px;
  padding: 16px 0 16px 46px;
}
@media (min-width: 768px) {
  .header__link-container {
    margin: 0;
    padding-left: 0;
    width: 100%;
  }
  .header__nav-item .header__link-container {
    align-items: flex-start;
  }
}
@media (min-width: 1150px) {
  .header__nav-item .header__link-container {
    align-items: center;
  }
  .header__nav-item:nth-child(n+3) .header__link-container,
  .header__order-item:nth-child(n+3) .header__link-container {
    align-items: flex-start;
    padding-top: 20px;
  }
}
.header__link-container--logo {
  justify-content: center;
  padding: 0;
}
@media (min-width: 768px) {
  .header__link-container--logo {
    justify-content: flex-start;
  }
}
@media (min-width: 768px) {
  .header__link-container--cart {
    padding-left: 82px;
    padding-right: 0;
    width: 264px;
  }
}
.header__link-container--cart span {
  margin-left: 5px;
}
@media (min-width: 768px) {
  .header__link-container--search {
    width: 82px;
  }
}
@media (min-width: 768px) {
  .header__nav-delivery {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    padding-top: 20px;
    justify-content: flex-end;
    align-items: flex-start;
    font-size: 15px;
    text-align: right;
    font-weight: 400;
  }
}
.index-page__hero-wrap {
  background-color: #fafafa;
  background-image: url("../img/hero-bg-mobile.jpg");
  background-position: center -15px;
  background-size: auto 130%;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .index-page__hero-wrap {
    background-image: url("../img/hero-bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .index-page__hero-wrap {
    background-image: url("../img/hero-bg.jpg");
    background-position: center 0;
    background-size: auto 119%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .index-page__hero-wrap {
    background-image: url("../img/hero-bg@2x.jpg");
  }
}
.index-page__hero {
  margin: 0 auto;
  padding: 0 30px;
  width: 320px;
  min-height: 283px;
  padding: 72px 30px 120px;
}
@media (min-width: 768px) {
  .index-page__hero {
    padding: 0 50px;
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .index-page__hero {
    padding: 0;
    width: 1050px;
  }
}
@media (min-width: 768px) {
  .index-page__hero {
    min-height: 625px;
    padding: 154px 90px 200px;
  }
}
@media (min-width: 1150px) {
  .index-page__hero {
    padding-top: 156px;
    padding-left: 175px;
    padding-right: 175px;
  }
}
.categories {
  position: relative;
  background-color: #fafafa;
}
@media (min-width: 768px) {
  .categories::after {
    position: absolute;
    content: "";
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 35px;
    background: url("../img/templates/white-wave-template.svg") left bottom / 39px auto repeat-x;
  }
  .categories::after {
    bottom: -10px;
    background-size: 38px auto;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .categories::after {
    height: 50px;
    background-size: 57px auto;
    background-position: -51px 100%;
  }
}
@media (min-width: 1150px) {
  .categories::after {
    background-size: 63px auto;
  }
}
.categories__container {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .categories__container {
    margin: 0 auto;
    flex-direction: row;
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .categories__container {
    width: 1050px;
  }
}
.categories__link {
  flex-grow: 1;
  position: relative;
  display: flex;
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;
  color: #fff;
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .categories__link {
    display: grid;
    grid-template-columns: min-content 1fr;
    font-size: 20px;
    line-height: 30px;
  }
}
.categories__link::after {
  position: absolute;
  top: 50%;
  right: 17.5%;
  transform: translate(50%, -50%);
  content: "";
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0.5;
}
@media (min-width: 1150px) {
  .categories__link::after {
    top: 48%;
    right: 11%;
  }
}
.categories__link:hover,
.categories__link:focus {
  transform: scaleY(1.04);
}
.categories__link:hover::after,
.categories__link:focus::after {
  opacity: 0.7;
}
.categories__link:hover .categories__inner-container,
.categories__link:focus .categories__inner-container {
  transform: scaleY(0.96) translateY(-5px);
}
.categories__link:active::after {
  opacity: 0.3;
}
.categories__link--left {
  transform-origin: bottom right;
}
.categories__link--left:hover {
  background-color: #59d5bc;
}
.categories__link--left::after {
  width: 40px;
  height: 62px;
  background-image: url("../img/icons/interior-category.svg");
