.reviews {
  @media (min-width: @tablet) {
    .reviews-bg;
  }

  @media (min-width: @desktop) {
    background: none;
  }
}

.reviews__container {
  position: relative;
  padding-top: 8px;
  padding-bottom: 45px;

  @media (min-width: @tablet) {
    padding-top: 83px;
    padding-bottom: 115px;
  }

  @media (min-width: @desktop) {
    padding-top: 78px;
    padding-left: 175px;
    padding-bottom: 160px;
    .reviews-bg;
  }
}

.reviews__title {
  margin-bottom: 13px;

  @media (min-width: @tablet) {
    padding-right: 190px;
    margin-bottom: 0;
  }
}

.reviews__item {
  margin: 0;
  margin-bottom: 29px;

  @media (min-width: @tablet) {
    margin-top: 38px;
    margin-bottom: 0;
  }
}

.reviews__text {
  margin: 0;
  margin-bottom: 35px;
  font-size: 15px;
  line-height: 30px;

  @media (min-width: @tablet) {
    font-size: 17px;
    margin-bottom: 60px;
    text-shadow: 0 0 3px @white;
  }

  @media (min-width: @desktop) {
    margin-bottom: 42px;
  }
}

.reviews__author-name {
  display: block;
  font-style: normal;
  font-weight: 700;
  color: @black;

  @media (min-width: @tablet) {
    max-width: 350px;
    font-size: 18px;//вот это похоже больше на опечатку дизайнера
  }

  @media (min-width: @tablet) {
    font-size: 17px;
  }
}

.reviews__author-link {
  display: block;
  max-width: 350px;
  line-height: 30px;
}

.reviews__navigation-wrap {
  display: flex;
  min-height: 30px;
  margin-bottom: 38px;

  @media (min-width: @tablet) {
    position: absolute;
    bottom: 116px;
    right: 50px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop) {
    right: 0;
    bottom: 162px;
  }
}

.reviews__feedback-button {
  @media (min-width: @tablet) {
    position: absolute;
    top: 80px;
    right: 50px;
    min-width: 175px;
    max-width: 300px;
  }

  @media (min-width: @desktop) {
    right: 0;
  }
}
